/* Disable pull to refresh */
body {
  overflow: hidden;
}

#root {
  height: 100vh;
  overflow-y: auto;
}
/* /pull to refresh */

#modal {
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: fixed; /* Position modal fixed to the viewport */
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7); /* Semi-transparent background overlay */
  z-index: 9999; /* Ensure modal appears above other content */
  overflow: auto; /* Enable scrolling if content exceeds viewport */
}

.card.modal-card-for-form {
  overflow: hidden;
  & form {
    display: flex;
    flex-direction: column;
    max-height: 100vh;
  }

  & .card-header, .card-footer {
    display: flex;
    justify-content: space-between;
    padding: 0;
    & button {
      border-radius: 0;
    }
  }
  & .card-header .heading {
    margin: 0.5rem;
    width: 100%;
    text-align: center;
  }
  & .card-footer {
    flex-direction: row-reverse;
  }
  
  & .card-body {
    overflow-y: auto;

    & .wrap-range-and-dates {
      display: flex;
      & .wrap-range-vertical {
        display: inline-flex;
        margin-right: 2rem;
  
        & div {
          display: flex;
          flex-direction: column;
          justify-content: space-around;
          & label {
            transform: translate(-40%) rotate(-90deg);
            transform-origin: center; /* Adjust origin as needed */
            writing-mode: horizontal-tb; /* Ensures proper rendering in modern browsers */
          }
        }
  
        & datalist {
          display: inline-flex;
          flex-direction: column-reverse;
          justify-content: space-between;
          margin-top: -9%;
          margin-bottom: -4%;
          margin-left: -2.5rem;
          margin-right: -1%;
          & option {
            padding: 0;
          }
        }
        & input[type='range'] {
          writing-mode: vertical-lr;
        }
      }
  
      & .wrap-dates input {
        width: 100%;
      }
    }
  }
}